import {IonAlert, IonSpinner} from '@ionic/react';
import React, {useState} from 'react';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import {RouteComponentProps} from "react-router";
import * as FIcon from "react-feather";
import {useApiToaster} from "../../helpers-ts";
import {PageApp} from '../../components/PageApp';

export const FabricanteEliminarCuenta: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const apiToaster = useApiToaster()
  const {loginStore} = rootStore
  const [Loading, setLoading] = useState(false)
  const [showAlert, setShowAlert] = useState(false)

  const enviar_mensaje = () => {
    // Error es 10 y Comentario y sugerencias es 20
    const data = {user_id: loginStore.id, modo_uso_app: loginStore.modo_uso_app, tipo: 30, mensaje: 'Eliminar cuenta'}
    setLoading(true)
    rootStore.environment.api.enviar_mensaje(data).then(result => {
      apiToaster(result,'Mensaje enviado al staff exitosamente', 'enviar')
      setLoading(false)
    }).catch(err => {
      setLoading(false)
    })
  }


  return <PageApp
    pageStyles={'background-blanco'}
    accion_boton_derecho={() => history.goBack()}
    boton_derecho={<FIcon.X size={30} color={'white'}/>}
    titulo_header={'Eliminar cuenta'}
    sub_titulo={` `}
    mostrar_barra={false}
  >
    <IonAlert
      isOpen={showAlert}
      onDidDismiss={() => setShowAlert(false)}
      header={'Eliminar cuenta'}
      message={'Confirma que desea eliminar la cuenta?'}
      buttons={[
        {
          text: 'Cancelar',
          role: 'cancel',
          cssClass: 'btn-secondary',
        },
        {
          text: 'Eliminar',
          cssClass: 'btn-primary',
          handler: () => {
            enviar_mensaje();
          }
        }
      ]}
    />
    <div className={'mx-3 py-2'}>
      <div className={'row'}>
        <p className={'text-gray mx-3'}>
          Est√° seguro de que desea eliminar su cuenta?
        </p>
        <button disabled={Loading} className={'btn btn-outline-primary btn-block mx-3 py-2'} onClick={() => setShowAlert(true)}>
          {Loading && <IonSpinner name="crescent"/>}
          {!Loading && <span>Eliminar cuenta</span>}
        </button>
      </div>
    </div>
  </PageApp>;
});

export default FabricanteEliminarCuenta;
